import { css } from 'emotion'

export const inputLabelCss = css({
	label: `inputLabel`,
	whiteSpace: 'nowrap',
	fontSize: `12px`,
	fontWeight: 'bold',
	color: 'gray',
	borderTop: `1px solid transparent`,
	borderBottom: `1px solid transparent`,
	paddingTop: 5,
	paddingBottom: 5,
})



